.circle
{
	/* display: inline; */
	position: relative;
	top: 12rem;
	border-radius: 50%;
	border: 2px solid transparent;
	width: 500px; 
	height: 500px; 
	background-color: rgb(163, 177, 163);
	box-shadow: 0px 1px 10px 10px rgb(103, 110, 103);
}


.hero:hover{
	
	transform:translateX(50%) translateY(-70%) ;
	transition-duration: 1s;
	backdrop-filter:blur(40px);
	box-shadow: 0px 1px 10px 10px rgba(232, 155, 54, 0.77);
	/* z-index: 11; */
}
