body{
	background-color: rgb(0, 0, 0);

	
}
.todo-container{
	width: 300px;
	padding: 10px 20px;
	font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
	background-color: white;
	border-radius:10px;

}


.todo-item{
	display: flex;
	justify-content: space-between;
	align-items: center;

}

.todo-btn{
	width: 100%;
	padding: 10px 20px;
	border-radius:10px;
	font-weight: bold;
	border: none;
	cursor: pointer;
}